@use "./variables.scss" as *;

.additional__container {
	display: flex;
	flex-direction: column;
	gap: 13px;
	margin-bottom: 34px;
}

@media (width >= $tablet-width) {
	.additional__container {
		display: grid;
		grid-template-columns: auto auto;
		gap: 32px;
	}

	.additional {
		margin-top: 83px;
	}

	.additional__title {
		margin-bottom: 57px;
	}
}

@media (width >= $desktop-width) {
	.additional__container {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr 1fr;
		gap: 80px;
	}

	.additional {
		margin-top: 86px;
	}
}
